<template>
  <div class="bg-white rounded-lg">
    <button class="border-2 border-black" @click="$router.back()">BACK</button>
    <div class="flex">wallUid: {{ wallUid }}</div>
    <div class="flex justify-center">
      <div>
        <div class="text-red-600">{{ errorMessage }}</div>
        <div class="w-96 h-96">
          <iframe
            class="md:rounded-xl md:overflow-hidden"
            ref="myiframe"
            src="/kbEditWall/main.html"
            :width="frameWidth"
            :height="frameHeight"
            scrolling="no"
            frameborder="0"
          >
          </iframe>
        </div>

        <div class="h-4"></div>
        <div class="w-96 h-96">
          <iframe
            class="md:rounded-xl md:overflow-hidden"
            ref="myd3"
            src="/testD3/main.html"
            width="100%"
            height="100%"
            scrolling="no"
            frameborder="0"
          >
          </iframe>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      iframeWin: null,
      errorMessage: "",
      frameWidth: "100%",
      frameHeight: "100%",
      wallUid: 0,
    };
  },
  mounted() {
    this.iframeWin = this.$refs.myiframe.contentWindow;
    this.wallUid = parseInt(this.$route.params.wallUid, 10);
    window.addEventListener("message", this.onMessage);
  },
  methods: {
    async onMessage(event) {
      if (event.data.cmd === undefined) return;
      const recv = event.data;

      if (recv.cmd == "kbEditWall.loadingComplete") {
        await this.$store.wait(1000);
        this.iframeWin.postMessage(
          {
            cmd: "kbEditWall.firstData",
          },
          "*"
        );
      } else if (recv.cmd == "kbEditWall.frameSize") {
        // this.frameWidth = recv.w;
        // this.frameHeight = recv.h;
      } else {
        console.error("EditWall unknown cmd", recv.cmd);
      }
    },
  },
};
</script>
